import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject } from 'rxjs';
import { IEntrega, IEntregaResponse } from '../models/entrega';
import { AuthService } from '@core/auth/auth.service';
import { Response } from '@shared/models/response-with-token';

@Injectable()
export class EntregasService {
    constructor(private http: HttpClient, private _authService: AuthService) { }
    /**
     * Funcion en el Servicio 
     * @param entregas 
     * @returns Crea un item nuevo
     */
    saveEntrega(entregas: IEntrega): Observable<IEntrega> {
        return this.http.post<IEntrega>(`${environment.stock.entregas}`, entregas);
    }

    /**
     * Funcion en el servicio
     * @param entregas
     * @returns Item actualizado actualizado
     */
    updateEntrega(entregas: IEntrega, identrega: number): Observable<IEntrega> {
        return this.http.put<IEntrega>(`${environment.stock.entregas}` + '/' + identrega, entregas);
    }

    /**
     * Funcion en el servicio
     * @param identrega 
     * @returns Elimina un item
     */
    deleteEntrega(identrega: number): Observable<IEntrega> {
        return this.http.delete<IEntrega>(`${environment.stock.entregas}` + '/' + identrega);
    }

    /**
     * 
     * @param identrega
     * @returns 
     */
    reporteEntrega(identrega: number): Observable<IEntregaResponse> {
        return this.http.get<IEntregaResponse>(`${environment.stock.reporte_entrega}` + identrega);
    }

    getDepositos(option?) {
        let f = '?';
        f += !option ? 'filter[activo]=1' : '';
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.depositos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getArticulosDepositos(deposito: number, idarticulo?: number) {
        let f = `?filter[deposito]=${deposito}`;
        f += idarticulo ? `&filter[idarticulo]=${idarticulo}` : '';

        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.view_stock_articulo_depositos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getUnidadesDeMedidaArticulos(idarticulo) {
        let f = `?filter[idarticulo]=${idarticulo}`
        f += `&expand=um`
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.articulo_unidad_medidas}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getItems(identrega?: number) {
        let f = `?filter[identrega]=${identrega}`;
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.view_stock_movimientos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }

    getVentas(idventagenerica?: number, filterLike?: string) {
        let f = `?&pagination=1&per-page=10&page=1`;
        if (idventagenerica) {
            f += `&filter[idventagenerica]=${idventagenerica}`;
        } else if (filterLike) {
            f += `&filter[or][0][numero_venta][like]=${filterLike}&filter[or][1][cliente][like]=${filterLike}`;
        }

        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.ventas.view_venta_genericas}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }

    getUnidadesFuncionales(idcomplejo?) {
        let f = idcomplejo ? `?filter[idcomplejo]=${idcomplejo}` : '';

        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.inmuebles.unidad_funcional.view_inm_unidad_funcional}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }

    getComplejos() {
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.inmuebles.complejos.view_inm_complejos}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }

    getUsuarios(option?) {
        let f = `?sort=asc`;
        f += !option ? '&filter[activo]=1' : '';
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.login.view_usuarios}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }

    /**
    * 
    * @param idordenservicio
    * @param queryParams 
    * @returns Verifica si el QR escaneado de la Orden de Servicio es valido.
    */
    getOrdenServicioEntregas(idordenservicio: number, queryParams: string): Observable<any> {
        return this.http.get<any>(`${environment.stock.view_stock_entregas}${queryParams}` + idordenservicio)
    }

    /**
    * 
    * @param idordenservicio
    * @param queryParams 
    * @returns Verifica si el QR escaneado de la Orden de Servicio es valido.
    */
    getOrdenServicioGenerico(idordenservicio: number, endpoint: string, queryParams: string): Observable<any> {
        return this.http.get<any>(`${endpoint}${queryParams}` + idordenservicio)
    }

    getUsuarioQR(codigo: number): Observable<any> {
        return this.http.get<any>(`${environment.login.view_usuarios}?filter[codigo]=` + codigo);
    }

    entregarEntrega(idempresa: number, payload): Observable<any> {
        return this.http.post<IEntrega>(`${environment.stock.entregar}` + idempresa, payload);
    }
    puedeCrearEntrega(): Observable<any> {
        return this.http.post<IEntrega>(`${environment.stock.stk_entrega_puede_crear}`, '');
    }
}