import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { IEntrega } from '../models/entrega';
import { EntregasService } from '../service/entregas.service';
import { Subject, Subscription, debounceTime, distinctUntilChanged, firstValueFrom, takeUntil } from 'rxjs';
import { MatTableDataSource } from '@angular/material/table'
import { SaveComponent } from './save/save.component';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { FormControl } from '@angular/forms';
import { TablaDinamicaComponent } from '@shared/components/tabla-dinamica/tabla-dinamica.component';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { environment } from 'environments/environment';
import { SearchService } from 'app/layout/common/search/search.service';
import { MatButton } from '@angular/material/button';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { FilterMenuComponent } from './filter-menu/filter-menu.component';
import { IFilter } from '@shared/models/filter';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { Router } from '@angular/router';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { PwaService } from '@shared/service/app/pwa.service';
import { LectorQrComponent } from '@shared/components/lector-qr/lector-qr.component';
@Component({
    selector: 'app-entregas',
    templateUrl: './entregas.component.html',
    styleUrls: ['./entregas.component.scss']
})
export class EntregasComponent implements OnInit, OnDestroy {
    entregas: IEntrega[];
    public dataSource: MatTableDataSource<IEntrega> = new MatTableDataSource();
    headers = ['Fecha Precarga', 'Fecha Entrega', 'Responsable', 'Observaciones', 'Deposito', 'Usuario Encargado', 'Estado', 'Acciones'];
    column_params = ['fecha_hora_precarga', 'fecha_hora_entrega', 'nombre_usuario', 'observaciones', 'descripcion_deposito', 'nombre_usuario_entrega', 'descripcion_estado', 'acciones'];
    funcionesObjeto = null;
    filtroBusqueda: string = '';
    filtersLike = ['nick_usuario', 'descripcion_deposito', 'nick_usuario_entrega', 'numero_venta'];
    filter: IFilter = {
        deposito: -1,
        filtroFechaInicio: '',
        filtroFechaFin: ''
    };
    @Input() extraParams = '';
    @Output() parametrosActualizados = new EventEmitter<any>();
    env = environment.stock.view_stock_entregas + '?sort=-fecha_hora_precarga';
    search: FormControl = new FormControl('');
    sortValue: string;
    FilterEventSub$: Subscription;
    SearchTextSub$: Subscription;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'border-primary text-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    orden_servicio: number;
    mostrarTextoOrdenServicio: boolean = true;
    buttonStyleOrdenServicio: string = 'default';
    ordenServicioScaneada = false;
    puede_crear: boolean = false;
    asignacion_entrega: number;

    constructor(
        private _entregasService: EntregasService,
        private _empresaService: EmpresaService,
        private confirm: FuseConfirmationService,
        private alert: AppAlertService,
        private dialogo: MatDialog,
        private _searchService: SearchService,
        private _menuService: JedsMenuService,
        private router: Router,
        public _headerTextService: HeaderTextService,
        private pwaService: PwaService

    ) {
        this._headerTextService.setTitulo(this.router);

        /**
         * Aca se declaran los botones que iran en la grilla sobre el apartado de acciones.
         */
        this.funcionesObjeto = [
            {
                nombre_boton: "Editar",
                functionName: 'editar',
                iconoAccion: (entrega: IEntrega) => entrega.puede_editar_entrega == 1 ? 'jedstion:editar' : '',
                iconoAccionado: (entrega: IEntrega) => '',
                iconoDeshabilitado: (entrega: IEntrega) => '',
            },
            {
                nombre_boton: "Eliminar",
                functionName: 'eliminar',
                iconoAccion: (entrega: IEntrega) => entrega.puede_eliminar == 1 ? 'jedstion:eliminar' : '',
                iconoAccionado: (entrega: IEntrega) => '',
                iconoDeshabilitado: (entrega: IEntrega) => '',
            },
            {
                nombre_boton: "Reporte",
                functionName: 'reporte',
                iconoAccion: (entrega: IEntrega) => 'jedstion:imprimir',
                iconoAccionado: (entrega: IEntrega) => '',
                iconoDeshabilitado: (entrega: IEntrega) => '',
            },
            {
                nombre_boton: "Entregar",
                functionName: 'entregar',
                iconoAccion: (entrega: IEntrega) => entrega.puede_editar_entrega == 1 && !entrega.fecha_hora_entrega ? 'jedstion:lector_qr' : '',
                iconoAccionado: (entrega: IEntrega) => '',//'jedstion:lector_qr_accionado',
                iconoDeshabilitado: (entrega: IEntrega) => '',//'jedstion:lector_qr_disabled',
            }
        ];
    }

    @ViewChild(TablaDinamicaComponent) tabla: TablaDinamicaComponent;
    ngOnInit(): void {
        this.puedeCrearEntrega();
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
                this.asignacion_entrega = empresa.asignacion_entrega;
            });

        // Load empresa data
        this._empresaService.getEmpresa();
        this.SearchTextSub$ = this._searchService.getSearchText().pipe( //Se obtienen los valores que coinciden con lo ingresado en la barra de busqueda
            debounceTime(750),
            distinctUntilChanged()
        )
            .subscribe(value => {
                this.filtroBusqueda = value;
                this.tabla.filters(this.filtroBusqueda);
            });

        this.FilterEventSub$ = this._searchService.getFilterEvent().subscribe(event => {
            this.onFilterMenu(this._searchService.getFilterOrigin())
        });
    }
    async puedeCrearEntrega(): Promise<any> {
        return new Promise<any>((resolve) => {
            this._entregasService.puedeCrearEntrega().subscribe({
                next: (data) => {
                    // Manejar la respuesta exitosa
                    if (data.success) {
                        this.puede_crear = data.result;
                        resolve(data);
                    }
                },
                error: (error) => {
                    // Manejar el error
                    console.log('No se pudo obtener puede_crear.')
                }
            });
        })
    }

    /**
    * Se escanea el QR de Orden de Servicio y se toma la idordenservicio.
    */
    ordenServicio() {
        let idordenservicio: number;
        let clave_os: string;

        this.dialogo.open(LectorQrComponent, {
            panelClass: 'ayuda-dialog',
        }).componentInstance.sendObject.subscribe((code) => {
            if (!code || code.length === 0) {
                this.alert.error('Cdigo QR vaco o invlido.');
                return;
            }

            let decodedText = JSON.parse(code); // Convierto el string en json

            for (const clave in decodedText) {
                clave_os = clave;
                idordenservicio = decodedText[clave];
            }

            let queryParams = `?filter[${clave_os}]=`;

            this._entregasService.getOrdenServicioEntregas(idordenservicio, queryParams).subscribe(response => {
                if (response?.result?.length > 0) {
                    const result = response.result[0];

                    if (this.asignacion_entrega === 0) {
                        this.orden_servicio = result.idventagenerica;
                        this.filter.orden_servicio = { idventagenerica: result.idventagenerica, cliente: result.cliente_venta, fecha: result.fecha_precarga };
                    } else if (this.asignacion_entrega === 1 && clave_os === 'idcomplejo') {
                        this.orden_servicio = result.idcomplejo;
                        this.filter.complejo = { idcomplejo: result.idcomplejo, descripcion_complejo: result.descripcion_complejo };
                    } else if (this.asignacion_entrega === 1 && clave_os === 'idunidadfuncional') {
                        this.orden_servicio = result.idunidadfuncional;
                        this.filter.unidad_funcional = { idunidadfuncional: result.idunidadfuncional, descripcion_complejo_uf: result.descripcion_complejo_uf };
                    }

                    this.extraParams = `&filter[${clave_os}]=${this.orden_servicio}`;

                    this.tabla.filters(this.filtroBusqueda); // Aplico filtro y renderizo la tabla
                } else {
                    this.alert.error('No hay items asociados a esa OS.');
                    this.extraParams = this.filtroBusqueda.replace(`&filter[${clave_os}]=${this.orden_servicio}`, ""); // Elimino el query params.
                    this.tabla.filters(this.filtroBusqueda); // Aplico filtro y renderizo la tabla
                }
            }, error => {
                console.error('Error al obtener venta:', error);
                this.tabla.filters(this.filtroBusqueda); // Aplico filtro y renderizo la tabla
            });

        });
    }

    onFilterMenu(origin: MatButton) {
        this._menuService
            .open(FilterMenuComponent, origin._elementRef, {
                filter: this.filter,
                data: this.tabla.getElementosExistentes()
            })
            .afterClosed()
            .subscribe({
                next: filter => {
                    if (filter) {
                        this.filter = filter;
                        this.extraParams = this.filter.deposito != -1 ? "filter[iddeposito]=" + this.filter.deposito + '&' : '';
                        this.extraParams += this.filter?.orden_servicio?.idventagenerica ? "filter[idventagenerica]=" + this.filter.orden_servicio?.idventagenerica + '&' : '';
                        this.extraParams += this.filter?.unidad_funcional?.idunidadfuncional ? "filter[idunidadfuncional]=" + this.filter.unidad_funcional?.idunidadfuncional + '&' : '';
                        this.extraParams += this.filter?.complejo?.idcomplejo ? "filter[idcomplejo]=" + this.filter.complejo?.idcomplejo + '&' : '';
                        this.extraParams += this.filter.usuario_responsable != '' ? "filter[idusuario]=" + this.filter.usuario_responsable.codigo + '&' : '';
                        this.extraParams += this.filter.usuario_encargado != '' ? "filter[usuario_entrega]=" + this.filter.usuario_encargado.codigo + '&' : '';
                        this.extraParams += this.filter.filtroFechaInicio != null ? "filter[fecha_precarga][gte]=" + this.filter.filtroFechaInicio + '&' : '';
                        this.extraParams += this.filter.filtroFechaFin != null ? "filter[fecha_precarga][lte]=" + this.filter.filtroFechaFin + '&' : '';
                        this.tabla.filters(this.filtroBusqueda);
                    }
                },
            });
    }

    /**
 * Esta es la funcion que se encarga de la columna acciones dentro de la grilla dinamica
 * @param event 
 */
    generarAcciones(event: { name: string, params: any }) {
        switch (event.name) {
            case 'editar':
                this.updateEntrega(event);
                break;
            case 'eliminar':
                this.deleteEntrega(event);
                break;
            case 'reporte':
                this.reporteEntrega(event);
                break;
            case 'entregar':
                this.entregarEntrega(event);
                break;
        }
    }
    entregarEntrega(event: { name: string; params: any; }) {
        let idUsuario: number = -1;
        this.dialogo.open(LectorQrComponent, {
            panelClass: 'ayuda-dialog'
        }).componentInstance.sendObject.subscribe((code) => {
            if (!code || code.length === 0) {
                this.alert.error('Cdigo QR vaco o invlido.');
                return;
            }

            let decodedText = JSON.parse(code); // Convierto el string en json

            const idusuario = parseInt(decodedText['idusuario'], 10);
            idUsuario = idusuario;
            this._entregasService.getUsuarioQR(idusuario).subscribe(response => {
                if (response?.result?.length > 0) {
                    const usuario = response.result[0];
                    this.confirm.open({
                        title: "Asignar Entrega: " + event.params.data.fecha_hora_precarga,
                        message: 'Asignar entrega al usuario ' + usuario.nombre + ' ' + usuario.apellido + '?',
                        dismissible: false,
                        actions: {
                            confirm: {
                                show: true,
                                color: 'warn',
                                label: 'Si',
                            },
                            cancel: {
                                show: true,
                                label: 'No',
                            },
                        },
                    })
                        .afterClosed()
                        .subscribe(res => {
                            if (res === 'confirmed') {
                                let payload = {
                                    "usuario_entrega": usuario.codigo
                                };
                                this._entregasService.entregarEntrega(event.params.data.identrega, payload).subscribe({
                                    next: (res) => {
                                        // Manejar la respuesta exitosa
                                        this.alert.success("Entrega asignada con xito.");
                                        if (res) {
                                            this.tabla.renderTabla()
                                        }
                                    },
                                    error: (error) => {
                                        // Manejar el error
                                        console.error('Error en el servicio:', error);
                                        this.alert.warning("Esta entrega no puede asignarse.");
                                    }
                                });
                            }
                        });

                } else {
                    this.alert.error('Usuario no encontrado.');
                }
            }, error => {
                console.error('Error al obtener usuario:', error);
            });
        });
    }

    /**
     * Esta funcion guarda un item
     */
    saveEntrega() {
        this.dialogo
            .open(SaveComponent, { panelClass: 'ayuda-dialog' })
            .componentInstance.creadoExitosamente
            .subscribe({
                next: () => {
                    this.alert.success("Entrega creada con xito.");
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion modifica un item
     * @param event 
     */
    updateEntrega(event) {
        this.dialogo
            .open(SaveComponent, { data: event.params.data, panelClass: 'my-dialog', autoFocus: false })
            .componentInstance.editadoExitosamente
            .subscribe({
                next: () => {
                    this.alert.success("Entrega editada con xito.");
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion elimina un item
     * @param event 
     */
    deleteEntrega(event) {
        this.confirm.open({
            title: "Eliminar Entrega: " + event.params.data.fecha_hora_precarga,
            message: `Desea eliminar la entrega realizada el: ${event.params.data.fecha_hora_precarga}; del depsito: ${event.params.data.descripcion_deposito} ?`,
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this._entregasService.deleteEntrega(event.params.data.identrega).subscribe({
                        next: (res) => {
                            // Manejar la respuesta exitosa
                            this.alert.success("Entrega eliminada con xito.");
                            if (res) {
                                this.tabla.renderTabla()
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error('Error en el servicio:', error);
                            this.alert.warning("Esta entrega no puede eliminarse. Tiene elementos asociados.");
                        }
                    });
                }
            });
    }

    /**
     * Esta funcion Genera un Reporte y lo muestra en una nueva pestaa
     */
    reporteEntrega(event) {
        const idEntrega = event.params.data.identrega;
        this._entregasService.reporteEntrega(idEntrega).subscribe(
            (data) => {
                if (data.success) {
                    const reportePath = data.path;
                    if (this.pwaService.isPwa()) {
                        this.router.navigate(['/reporte', reportePath]);
                    } else {
                        const reporteUrl = `${environment.baseRest}/${reportePath}`;
                        window.open(reporteUrl, '_blank');
                    }
                } else {
                    console.error('Error al generar el reporte');
                }
            },
            (error) => {
                console.error('Error en la solicitud del reporte', error);
            }
        );
    }

    ngOnDestroy(): void {
        this._searchService.unsubscribe();
        this.dialogo.closeAll();
    }
}