import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { IEntrega } from '../../models/entrega';
import { EntregasService } from '../../service/entregas.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { BehaviorSubject, Observable, Subject, catchError, debounceTime, distinctUntilChanged, finalize, firstValueFrom, map, of, startWith, switchMap, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { MatTableDataSource } from '@angular/material/table';
import { DatePipe } from '@angular/common';
import { MY_DATE_FORMATS } from '@shared/constants/date-format';
import { IArticulo } from 'app/modules/stock/articulos/models/articulo';
import { IDeposito } from 'app/modules/stock/depositos/models/deposito';
import { IUsuario } from '@core/user/user.types';
import { IAlertConfig } from '@shared/models/alert';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { IUnidadFuncional } from 'app/modules/inmuebles/unidades-funcionales/models/unidad-funcional';
import { IComplejo } from 'app/modules/inmuebles/complejos/models/complejos';
import { LectorQrComponent } from '@shared/components/lector-qr/lector-qr.component';
import { environment } from 'environments/environment';

@Component({
    selector: 'app-save-entregas',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss'],
    providers: [{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }]
})
export class SaveComponent implements OnInit {
    // Necesarios para la app
    title: string;
    maxPalabras: number = 50;
    minDate: Date;
    maxDate: Date;
    user: any;
    showAlert = false;
    _alertConfig: IAlertConfig;
    color_boton: string;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    @Output() public creadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    @Output() public editadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    deposito_default = 0;
    asignacion_entrega = null;

    // Particulares del modulo
    columns = ['codigointerno', 'articulo', 'unidadmedida', 'cantidad', 'acciones'];
    form: FormGroup;
    form_carga_articulos: FormGroup;
    entrega: IEntrega;
    articulos: any[] = [];
    articulos_list = [];
    unidades_de_medida: any[] = [];
    usuarios: IUsuario[] = [];
    depositos: IDeposito[] = [];
    mov_art_list: any[] = [];
    ventas: any[] = [];
    unidades_funcionales: IUnidadFuncional[] = [];
    complejos: IComplejo[] = [];
    filteredUsuarios: Observable<IUsuario[]>;
    filteredDepositos: Observable<IDeposito[]>;
    filteredArticulos: Observable<IArticulo[]>;
    filteredVentas = new BehaviorSubject<any[]>([]);
    filteredUnidadesFuncionales: Observable<IUnidadFuncional[]>;
    filteredComplejos: Observable<IComplejo[]>;
    public dataSource: MatTableDataSource<any> = new MatTableDataSource();
    esEdicion: boolean;
    orden_servicio: number;

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _entregasService: EntregasService,
        private _empresaService: EmpresaService,
        private datePipe: DatePipe,
        private alert: AppAlertService,
        public dialogRef: MatDialogRef<SaveComponent>,
        private dialogo: MatDialog,
    ) {
        this.entrega = this.data ? this.data : null;
        this.esEdicion = this.entrega ? true : false;
        this.title = this.entrega?.identrega ? `Editar Entrega ${this.entrega.fecha_precarga} - ${this.entrega.hora_precarga}` : `Nueva Entrega`;
        this.user = JSON.parse(localStorage.getItem('user'))
        // Se define minDate como null o cualquier otra fecha lmite que desees permitir hacia atras
        this.minDate = null;
        // Define maxDate como el dia de hoy
        this.maxDate = new Date();

        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
                this.deposito_default = empresa.deposito_default;
                this.asignacion_entrega = empresa.asignacion_entrega;
            });
    }

    ngOnInit(): void {
        // Load empresa data
        this._empresaService.getEmpresa();
        this.newForm();
        this.exist();
        this.subscribeAlert();
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
            next: (config: IAlertConfig) => {
                this.showAlert = true;
                this._alertConfig = {
                    ...config,
                    appearance: 'border',
                };
                let time = 2;
                timer(100, 1500)
                    .pipe(
                        finalize(() => {
                            this.showAlert = false;
                            this._alertConfig = null;
                        }),
                        takeWhile(() => time > 0),
                        takeUntil(this._unsubscribeAll),
                        tap(() => (time -= 1))
                    )
                    .subscribe();
            },
        });
    }

    newForm() {
        const [dia, mes, anio] = this.entrega ? this.entrega.fecha_precarga.split('/') : ''; //Se hace esto para poder manejar el formato que se cambio en el render del componente de tabla dinamica.

        if (this.entrega) {
            Promise.all([/* this.getVentas(),  */this.getItems(this.entrega.identrega)]).then(([/* ventas,  */items]) => {
                // this.ventas = ventas;
                this.mov_art_list = items.map(m => {
                    return {
                        idarticulo: m.idarticulo,
                        cantidad: parseFloat(m.cantidad),
                        idunidadmedida: m.idunidadmedida,
                        descripcion_articulo: m.descripcion_articulo,
                        descripcion_um: m.descripcion_um,
                        codigo_interno: m.codigo_interno_articulo,
                        stock: m.stock_minimo_articulo
                    }
                });

                this.dataSource = new MatTableDataSource(this.mov_art_list);

                if (this.mov_art_list.length != 0) {
                    this.form.get('deposito_entrega').disable();
                } else {
                    this.form.get('deposito_entrega').enable();
                }
            });
        }

        this.form = new FormGroup({
            fecha: new FormControl((this.entrega?.fecha_precarga ? this.datePipe.transform(new Date(`${mes}/${dia}/${anio}`), 'yyyy-MM-dd') : this.datePipe.transform(new Date(), 'yyyy-MM-dd')), [Validators.required]),
            hora: new FormControl((this.entrega?.hora_precarga ? this.entrega.hora_precarga : this.datePipe.transform(new Date(), 'HH:mm:ss')), Validators.required),
            usuario_entrega: new FormControl((this.entrega?.usuario_entrega ? { codigo: this.entrega.usuario_entrega, nombre: this.entrega.nombre_usuario_entrega } : '')),
            deposito_entrega: new FormControl((this.entrega ? { descripcion: this.entrega.descripcion_deposito, iddeposito: this.entrega.iddeposito } : ''), Validators.required),
            observaciones: new FormControl((this.entrega ? this.entrega.observaciones : ''), [Validators.maxLength(this.maxPalabras)]),
        });
        this.form_carga_articulos = new FormGroup({
            articulo: new FormControl((''), Validators.required),
            unidadmedida: new FormControl((''), Validators.required),
            cantidad: new FormControl((''), [Validators.required, Validators.min(1)]),
            stock_existente: new FormControl((''))
        });

        this.updateFormValidators();
    }

    /**
    * Si existe un stockinicial le mando 'true' a fondos y nada a TM para que me traiga todos los fondos y TMs al editar.
    * Caso contrario, si se va a crear un stockinicial, llamo a los fondos sin enviarle un parametro asi me trae solo los activos y
    * le envio un paramtro a los tipos de stockinicials para que me traiga los activos.
    */
    exist() {
        if (this.entrega?.idusuario) {
            this.getUsuarios(true);
        } else if (this.entrega?.identrega) {
            this.getDepositos(true);
        } else {
            this.getUsuarios();
            this.getDepositos();
        }

        if (this.entrega?.iddeposito) {
            this.getArticulosDepositos(this.entrega.iddeposito) //...Buscamos las UM que posee ese articulo.
        }

        if (this.asignacion_entrega === 0) {
            let venta = null;

            if (this.entrega?.idventagenerica) {
                venta = { idventagenerica: this.entrega.idventagenerica, cliente: this.entrega.cliente_venta, fecha: this.entrega.fecha_venta, numero_venta: this.entrega.idventagenerica };
            } else if (this.entrega?.idventa) {
                venta = { idventa: this.entrega.idventa, cliente: this.entrega.cliente_venta, fecha: this.entrega.fecha_venta, numero_venta: this.entrega.idventa }
            }

            this.form.addControl('venta', new FormControl((this.entrega ? venta : ''), Validators.required));
            this.getVentas();

            // Escuchar los cambios en el input y filtrar los resultados
            this.form.get('venta').valueChanges.pipe(
                debounceTime(300),  // Evita demasiadas llamadas consecutivas
                distinctUntilChanged(),
                switchMap(value => {
                    const idventagenerica = typeof value === 'object' ? value.idventagenerica : null;
                    const filterLike = typeof value === 'string' ? value : null;
                    return this._entregasService.getVentas(idventagenerica, filterLike).pipe(
                        catchError(() => of({ list: [] })) // Manejo de errores
                    );
                })
            ).subscribe(response => {
                this.filteredVentas.next(response.list.sort((a, b) => b.idventagenerica - a.idventagenerica) || []); // Actualizar el BehaviorSubject
            });

        } else if (this.asignacion_entrega === 1) {
            this.form.addControl('unidad_funcional', new FormControl((this.entrega?.idunidadfuncional ? { idunidadfuncional: this.entrega.idunidadfuncional, descripcion_complejo_uf: this.entrega.descripcion_unidad_funcional, idcomplejo: this.entrega.idcomplejo, descripcion_complejo: this.entrega.descripcion_complejo } : ''),));
            this.form.addControl('complejo', new FormControl((this.entrega?.idcomplejo ? { idcomplejo: this.entrega.idcomplejo, descripcion_complejo: this.entrega.descripcion_complejo } : ''),));
            this.getComplejos();
            this.getUnidadesFuncionales();
        }
    }

    // ################# Carga De Movimientos #################

    /**
     * Esta funcion aade un nuevo item a la grilla dentro del saveComponent.
     * @var art_descripcion busca en el arreglo correspondiente un elemento con el mismo id para imprimir su descripcion.
     * @var um_descripcion busca en el arreglo correspondiente un elemento con el mismo id para imprimir su descripcion.
     * @var art_stk_ini Encapsula un objeto con los datos que deseamos mostrar en la tabla.
     */
    addMovimiento(movimiento) {
        if (this.form.get('deposito_entrega').value != '') {

            let deposito_entrega_value = this.form.get('deposito_entrega').value.iddeposito;
            if (typeof this.form.get('deposito_entrega').value == 'string') { //En caso de edicion...
                let d = this.depositos.find(d => d.descripcion === this.form.get('deposito_entrega').value);
                deposito_entrega_value = d.iddeposito;
            }

            if (movimiento) {
                let movimiento_articulo = {                                     //Encapsulo los datos que me interesan mostrar en la grilla.
                    idarticulo: movimiento.articulo.idarticulo,
                    cantidad: movimiento.cantidad,
                    idunidadmedida: movimiento.unidad_medida,
                    descripcion_articulo: movimiento.articulo.descripcion,
                    descripcion_um: movimiento.unidadmedida_descripcion,
                    codigo_interno: movimiento.articulo.codigo_interno,
                    stock: movimiento.stock,    //Stock restante de ese articulo con esa um...
                }

                this.mov_art_list = [movimiento_articulo, ...this.mov_art_list]; // Se agrega el nuevo articulo al comienzo de la lista para que siempre aparezca primero en la grilla
                this.dataSource = new MatTableDataSource(this.mov_art_list); //Actualizo la tabla

                // Deshabilito el control Deposito
                if (this.mov_art_list.length != 0) {
                    this.form.get('deposito_entrega').disable();
                }

                // Actualizar validaciones segn el estado de la grilla
                this.updateFormValidators()

                this.limpiarCasillero('carga_exitosa'); // Limpio casillerros para que este listo para volver a cargar
                this.alert.success("Artculo aadido con xito");
            }
        } else {
            this.alert.warning("Seleccione un Depsito de origen")
        }
    }

    /**
     * El resultado de esta operacin es un nuevo 
     * array que contiene todos los elementos de la lista original, 
     * excepto el artculo que se desea eliminar.
     * @param movimiento_articulo 
     */
    deleteMovimiento(movimiento_articulo) {
        this.mov_art_list.forEach((mov) => {
            if (mov.idarticulo == movimiento_articulo.idarticulo && mov.idunidadmedida == movimiento_articulo.idunidadmedida) {
                mov.stock = `${Number(mov.stock) + mov.cantidad}`;
            };
        });

        this.mov_art_list = this.mov_art_list.filter((item) => { return item !== movimiento_articulo });
        this.dataSource = new MatTableDataSource(this.mov_art_list);

        // Habilito el control Deposito si la lista de movimientos est vaca
        if (this.mov_art_list.length === 0) {
            this.form.get('deposito_entrega').enable();
        }

        // Actualizar validaciones segn el estado de la grilla
        this.updateFormValidators();

        this.alert.success("Artculo eliminado con xito")
    }

    /**
    * Esta funcion maneja las cambios de seleccion que puedan tener los combos.
    * @param $event 
    */
    seleccionItem($event, info?) {
        if ($event.idcomplejo) {
            this.getUnidadesFuncionales($event.idcomplejo) //...Buscamos las UF que posee ese complejo.
        } else if ($event.idcomplejo === null) {
            this.getUnidadesFuncionales($event.idcomplejo) //...Buscamos las UF que posee ese complejo.
        } else if ($event === 'complejo' && info === 'reset') {
            this.getUnidadesFuncionales() //...Buscamos las UF que posee ese complejo.
        }

        //Al seleccionar un articulo...
        if ($event.idarticulo) {
            this.form_carga_articulos.get('unidadmedida').enable()
            this.getUnidadesDeMedidaArticulos($event.idarticulo) //...Buscamos las UM que posee ese articulo.

            // Muevo el foco al campo "cantidad" luego de un pequeo retraso.
            // Lo encapsulo dentro de `setTimeout` porque espero a que el DOM renderice el input "cantidad".
            setTimeout(() => {
                const cantidadField = document.querySelector('[formControlName="cantidad"]') as HTMLInputElement;
                cantidadField?.focus();
            }, 1);
        }

        // - Mostrar Stock -
        //Al seleccionar la UM...
        if ($event.um) {
            //Consultamos s existe un articulo con esa UM y utilizamos la respuesta para ver si existe un movimiento con ese articulo y esa UM en los movimientos.
            let articulo_um = this.unidades_de_medida.find(a => a.idarticulo === this.form_carga_articulos.get('articulo').value.idarticulo && a.idunidadmedida === this.form_carga_articulos.get('unidadmedida').value);
            let stock; //variable auxiliar   
            let cantidad_existente = 0;

            let existeMovimiento = this.mov_art_list.length > 0 ? true : false; //Consultamos s hay movimientos

            if (existeMovimiento && this.esEdicion === false) { //S existe minimo un movimiento y es una crecion...
                let articulos_existentes = this.mov_art_list.filter(a => a.idarticulo === articulo_um.idarticulo && a.idunidadmedida === articulo_um.idunidadmedida); //Se hace esto para mantener el stock actualizado, en caso de haber sido modificado.
                //Si existen movimientos de articulos con la misma UM...
                articulos_existentes.forEach(ae => {
                    cantidad_existente = cantidad_existente + Number(ae.cantidad); //...sumamos la cantidad que equivalen
                })
                stock = `${Number(articulo_um.stock) - cantidad_existente}`; //...Se le asigna el stock restante...
            } else {
                stock = articulo_um.stock; //..Sino el stock que traiga ese articulo con esa UM del servicio.
            }
            this.form_carga_articulos.get('stock_existente').setValue(stock) //Lo mostramos.
        }

        //Al seleccionar un deposito...
        if ($event.iddeposito) {
            this.form_carga_articulos.enable();
            this.getArticulosDepositos($event.iddeposito) //...Buscamos las UM que posee ese articulo.
        }
    }

    /**
    * Funcion para limpiar campos
    */
    limpiarCasillero(input) {

        if (this.form.get(input)) {
            this.form.get(input).setValue('');
            this.seleccionItem(input, 'reset');
        }

        if (input === 'complejo' && this.form.get('unidad_funcional').value) {
            this.form.get('unidad_funcional').setValue('');
        }

        if (input === 'articulo') {
            this.form_carga_articulos.get('unidadmedida').disable();
            this.form_carga_articulos.get('unidadmedida').setValue('');
            this.form_carga_articulos.get('articulo').setValue('');
            this.form_carga_articulos.get('stock_existente').setValue('');
            this.form_carga_articulos.get('cantidad').setValue('');
        }

        if (input === 'deposito_entrega') {
            this.form_carga_articulos.disable();
            this.form_carga_articulos.get('unidadmedida').setValue('');
            this.form_carga_articulos.get('articulo').setValue('');
            this.form_carga_articulos.get('stock_existente').setValue('');
            this.form_carga_articulos.get('cantidad').setValue('');
        }

        if (input === 'carga_exitosa') {
            this.form_carga_articulos.get('unidadmedida').setValue('');
            this.form_carga_articulos.get('articulo').setValue('');
            this.form_carga_articulos.get('stock_existente').setValue('');
            this.form_carga_articulos.get('cantidad').setValue('');
        }
    }

    onCargar($event?: KeyboardEvent) {
        if ($event?.key === 'Enter') {
            $event.preventDefault();
        }

        if (this.form_carga_articulos.get('cantidad').value <= 0) {
            return this.alert.warning('Por favor, Ingrese una cantidad mayor a cero');
        }

        if (!this.form_carga_articulos.get('unidadmedida').value || !this.form_carga_articulos.get('articulo').value || !this.form_carga_articulos.get('cantidad').value) {
            return this.alert.warning('Por favor, complete todos los campos de la carga de articulo');
        }

        if (!this.form_carga_articulos.valid) {
            return this.alert.error('Debe seleccionar un artculo');
        } else {
            this.articulos.forEach(articulo => {
                if (articulo.idarticulo === this.form_carga_articulos.get('articulo').value.idarticulo && articulo.idunidadmedida === this.form_carga_articulos.get('unidadmedida').value) {
                    if (this.form_carga_articulos.get('cantidad').value > articulo.stock) {
                        this.form_carga_articulos.get('cantidad').setValue('');
                        return this.alert.warning('No hay stock suficiente');
                    }

                    let existeMovimiento = this.mov_art_list.find(movimiento => movimiento.idarticulo === this.form_carga_articulos.get('articulo').value.idarticulo && movimiento.idunidadmedida === this.form_carga_articulos.get('unidadmedida').value/*  && movimiento.idventa === this.form_carga_articulos.get('venta').value.idventa */);

                    if (existeMovimiento) {
                        this.form_carga_articulos.get('unidadmedida').setValue('');
                        this.form_carga_articulos.get('stock_existente').setValue('');
                        return this.alert.warning('Ya existe un movimiento de ese articulo con esa unidad de medida asociado a esa Orden de Servicio');
                    }

                    if (this.form_carga_articulos.get('cantidad').value <= articulo.stock && this.form_carga_articulos.get('cantidad').value > 0) {
                        let unidadmedida = this.unidades_de_medida.find(um => um.idunidadmedida === this.form_carga_articulos.get('unidadmedida').value); //Traigo una UM que coincida con mi seleccion para poder utilizar su descripcion.

                        let movimiento_articulo = {
                            articulo: this.form_carga_articulos.get('articulo').value,
                            unidad_medida: this.form_carga_articulos.get('unidadmedida').value,
                            unidadmedida_descripcion: unidadmedida.descripcion_um,
                            stock: `${Number(articulo.stock) - this.form_carga_articulos.get('cantidad').value}`,
                            cantidad: this.form_carga_articulos.get('cantidad').value
                        }

                        this.addMovimiento(movimiento_articulo)
                    }
                }
            })
        }

        // Mueve el foco al campo "articulo" despus de la carga
        this.form_carga_articulos.get('articulo').reset({ value: '' });
        const articuloField = document.querySelector('[formControlName="articulo"]') as HTMLInputElement;
        articuloField?.focus();
    }

    onSubmit() {
        // Verificar la longitud de las observaciones
        if (!this.isPalabraMaxLengthReached()) {
            return this.alert.error(`La longitud mxima de observaciones es de ${this.maxPalabras} caracteres.`);
        }

        if (!this.form.valid) {
            return this.alert.error('Revise Los Datos Ingresados');
        } else {

            if (this.asignacion_entrega === 1) {
                const existe_complejo_uf = this.form.get('unidad_funcional').value && this.form.get('complejo').value;

                if (existe_complejo_uf) {
                    const la_uf_no_pertenece_al_complejo = this.form.get('unidad_funcional').value?.idcomplejo != this.form.get('complejo').value?.idcomplejo;

                    if (la_uf_no_pertenece_al_complejo) {
                        this.form.get('complejo').setValue('');
                        return this.alert.error('La unidad Funcional no pertenece a ese complejo');
                    }
                } else if (!this.form.get('unidad_funcional').value && !this.form.get('complejo').value) {
                    return this.alert.error('Debe elegir una Unidad Funcional y/o Complejo');
                }
            }

            let movimientos = this.mov_art_list.map(objeto => ({   //Encapsulo solamente los datos que utilizar de los articulos...
                idarticulo: objeto.idarticulo,
                cantidad: objeto.cantidad,
                idunidadmedida: objeto.idunidadmedida
            }))

            if (movimientos.length === 0) {
                return this.alert.warning('Debe ingresar un articulo a la orden de entrega')
            }

            const fecha = this.form.get('fecha').value;
            const hora = this.form.get('hora').value;

            // Se formatea la fecha nuevamente al formato deseado (YYYY-MM-DD)
            const fecha_formateada = this.datePipe.transform(fecha, 'yyyy-MM-dd');

            // Se concatena a ambas fecha y hora
            const fecha_hora_precarga = `${fecha_formateada} ${hora}`;

            const payload = {
                entrega: {
                    idusuario: this.user.id,
                    fecha_hora_precarga: fecha_hora_precarga,
                    iddeposito: this.form.get('deposito_entrega').value.iddeposito,
                    usuario_entrega: this.form.get('usuario_entrega').value?.codigo ? this.form.get('usuario_entrega').value.codigo : null,
                    observaciones: this.form.get('observaciones').value,
                    idventa: this.form.get('venta')?.value?.idventa ? this.form.get('venta').value.idventa : null,
                    idventagenerica: this.form.get('venta')?.value?.idventagenerica ? this.form.get('venta').value.idventagenerica : null,
                    idcomplejo: this.form.get('complejo')?.value?.idcomplejo ? this.form.get('complejo').value.idcomplejo : null,
                    idunidadfuncional: this.form.get('unidad_funcional')?.value?.idunidadfuncional ? this.form.get('unidad_funcional').value.idunidadfuncional : null,
                },
                movimientos: movimientos
            }

            if (this.entrega?.identrega) {
                this._entregasService.updateEntrega(payload, this.entrega.identrega)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.editadoExitosamente.emit();
                                this.dialogRef.close(data);
                            }
                        }
                    });
            } else {
                this._entregasService.saveEntrega(payload)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.creadoExitosamente.emit();
                                this.dialogRef.close(data);
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }

    // ################# Metodos de escaner QR #################

    /**
    * Se escanea el QR de la Credencial de Usuario y se toma el idusuario.
    */
    credencialUsuario() {
        this.dialogo.open(LectorQrComponent, {
            panelClass: 'ayuda-dialog'
        }).componentInstance.sendObject.subscribe((code) => {
            if (!code || code.length === 0) {
                return this.alert.error('Cdigo QR vaco o invlido');
            }

            let decodedText = JSON.parse(code); // Convierto el string en un json

            const idusuario = parseInt(decodedText['idusuario'], 10);
            this._entregasService.getUsuarioQR(idusuario).subscribe(response => {
                if (response?.result?.length > 0) {
                    const usuario = response.result[0];
                    this.form.get('usuario_entrega').setValue(usuario);
                } else {
                    return this.alert.error('Usuario no encontrado');
                }
            }, error => {
                console.error('Error al obtener usuario:', error);
            });
        });
    }

    /**
    * Se escanea el QR de la Etiqueta de articulo y se toma el idarticulo.
    */
    etiquetaArticulo() {
        let deposito = this.form.get('deposito_entrega').value;

        if (!deposito) {
            return this.alert.error('Debe seleccionar un depsito');
        }

        this.dialogo.open(LectorQrComponent, {
            panelClass: 'ayuda-dialog'
        }).componentInstance.sendObject.subscribe((code) => {

            if (!code || code.length === 0) {
                return this.alert.error('Cdigo QR vaco o invlido');
            }

            let decodedText = JSON.parse(code); // Convierto el string en un json

            const idarticulo = parseInt(decodedText['idarticulo'], 10);
            this._entregasService.getArticulosDepositos(deposito['iddeposito'], idarticulo).subscribe(response => {
                if (response?.list?.length > 0) {
                    const articulo = response.list[0];
                    this.form_carga_articulos.get('articulo').setValue(articulo);
                } else {
                    return this.alert.error('No hay stock del artculo en el depsito');
                }
            }, error => {
                console.error('Error al obtener artculo:', error);
            });
        });
    }

    /**
    * Se escanea el QR de Orden de Servicio y se toma la idordenservicio.
    */
    ordenServicio() {
        let idordenservicio: number;
        let clave_os: string;
        let endpoint: string;
        let queryParams: string;

        this.dialogo.open(LectorQrComponent, {
            panelClass: 'ayuda-dialog'
        }).componentInstance.sendObject.subscribe((code) => {
            if (!code || code.length === 0) {
                return this.alert.error('Cdigo QR vaco o invlido');
            }

            let decodedText = JSON.parse(code); // Convierto el string en json

            // Encapsulo los datos que voy a utilizar
            for (const clave in decodedText) {
                clave_os = clave;
                idordenservicio = decodedText[clave];
            }

            // Defino el endpoint segun cada asignacion
            if (this.asignacion_entrega === 0 && clave_os === 'idventagenerica') {
                endpoint = `${environment.ventas.view_venta_genericas}`;
            } else if (this.asignacion_entrega === 1 && clave_os === 'idcomplejo') {
                endpoint = `${environment.inmuebles.complejos.view_inm_complejos}`;
            } else if (this.asignacion_entrega === 1 && clave_os === 'idunidadfuncional') {
                endpoint = `${environment.inmuebles.unidad_funcional.view_inm_unidad_funcional}`;
            } else {
                return this.alert.error('Orden de Servicio no encontrada');
            }

            queryParams = `?filter[${clave_os}]=`;

            this._entregasService.getOrdenServicioGenerico(idordenservicio, endpoint, queryParams).subscribe(response => {
                if (response?.result?.length > 0) {
                    const result = response.result[0];

                    if (this.asignacion_entrega === 0) {
                        this.orden_servicio = result.idventagenerica;
                        this.form.get('venta').setValue(result);
                    } else if (this.asignacion_entrega === 1 && clave_os === 'idcomplejo') {
                        this.form.get('complejo').setValue(result);
                        this.orden_servicio = result.idcomplejo;
                    } else if (this.asignacion_entrega === 1 && clave_os === 'idunidadfuncional') {
                        this.form.get('unidad_funcional').setValue(result);
                        this.orden_servicio = result.idunidadfuncional;
                    }
                } else {
                    return this.alert.error('No hay items asociados a esa OS');
                }
            }, error => {
                console.error('Error al obtener venta:', error);
            });

        });
    }

    // ################# Metodos de comunicacion con el service #################

    async getUsuarios(option?) {
        const Usuarios = await firstValueFrom(this._entregasService.getUsuarios(option));
        new Promise(async (resolve) => {
            this.usuarios = Usuarios.list;
            this.filteredUsuarios = this.form.get('usuario_entrega').valueChanges.pipe(
                startWith(''),
                map((value) => this._filterUsuarios(value)),
            );

            resolve(true);
        })
    };

    async getDepositos(option?) {
        const Depositos = await firstValueFrom(this._entregasService.getDepositos(option));
        new Promise(async (resolve) => {
            this.depositos = Depositos.list;

            // Filtrar y autoasignar el depsito por defecto
            const depositoPorDefecto = this.depositos.find(deposito => deposito.iddeposito === this.deposito_default);
            if (depositoPorDefecto && !this.entrega) {
                this.form.get('deposito_entrega').setValue(depositoPorDefecto);
            }

            this.filteredDepositos = this.form.get('deposito_entrega').valueChanges.pipe(
                startWith(''),
                map((value) => this._filterTextGenerico(value, this.depositos, 'descripcion')),
            );

            resolve(true);
        })
    };

    /**
    * Funcion que lista los complejos.
    */
    async getComplejos(): Promise<any> {
        const complejos = await firstValueFrom(this._entregasService.getComplejos());
        this.complejos = complejos.list;
        this.filteredComplejos = this.form.get('complejo').valueChanges.pipe(
            startWith(''),
            map((value) => this._filterTextGenerico(value, this.complejos, 'descripcion_complejo')),
        );
    }

    /**
    * Funcion que lista las unidades funcionales.
    */
    async getUnidadesFuncionales(idcomplejo?): Promise<any> {
        const unidades_funcionales = await firstValueFrom(this._entregasService.getUnidadesFuncionales(idcomplejo));
        this.unidades_funcionales = idcomplejo === null ? unidades_funcionales.list.filter(uf => uf.idcomplejo === null) : unidades_funcionales.list;
        this.filteredUnidadesFuncionales = this.form.get('unidad_funcional').valueChanges.pipe(
            startWith(''),
            map((value) => this._filterTextGenerico(value, this.unidades_funcionales, 'descripcion_complejo_uf')),
        );
    }

    /**
    * Funcion que lista las ventas.
    */
    async getVentas(): Promise<void> {
        try {
            const ventas = await firstValueFrom(this._entregasService.getVentas());
            this.ventas = ventas.list.sort((a, b) => b.idventagenerica - a.idventagenerica) || [];
            this.filteredVentas.next(this.ventas); // Inicializar con todas las ventas
        } catch (error) {
            console.error('Error obteniendo ventas:', error);
            this.filteredVentas.next([]);
        }
    }


    /**
     * Listamos los movimientos asociados a este registro
     * @param identrega 
     * @returns 
     */
    async getItems(identrega): Promise<any> {
        return new Promise(async (resolve) => {
            const items = await firstValueFrom(this._entregasService.getItems(identrega));
            this.mov_art_list = items.list;
            resolve(this.mov_art_list);
        })
    }

    /**
    * Esta funcion solicita las unidades de medida que posee un articulo.
    * @param idarticulo 
    */
    getUnidadesDeMedidaArticulos(idarticulo) {
        new Promise(async (resolve) => {
            let um_base; // Asigno una variable global para que cada UM sepa cual es la um base (factor = '1.00')
            this.unidades_de_medida = this.articulos.filter(a => a.idarticulo === idarticulo && a.factor_um != null).sort((a, b) => a.factor_um - b.factor_um).map(a => {
                //Convierto en numero
                a.factor_um = parseFloat(a.factor_um);

                // Almaceno solo la um base de un articulo 
                if (a.factor_um === 1) {
                    um_base = a.um;
                }

                return { idarticulo: a.idarticulo, idunidadmedida: a.idunidadmedida, descripcion_um: a.um, stock: a.stock, factor_um: a.factor_um, um_base: um_base }
            });

            // Autoasigna la primera unidad de medida al control del formulario
            if (this.unidades_de_medida.length > 0) {
                this.form_carga_articulos.get('unidadmedida').setValue(this.unidades_de_medida[0].idunidadmedida);
                this.seleccionItem({ um: this.unidades_de_medida[0].idunidadmedida });
            }
            resolve(true);
        })
    }

    /**
    * Funcion que lista los articulos que posee un dposito.
    */
    async getArticulosDepositos(iddeposito: number) {
        const articulos_um_deposito = await firstValueFrom(this._entregasService.getArticulosDepositos(iddeposito)); //solicitamos los aticulos que esten en un deposito
        new Promise(async (resolve) => {
            this.articulos = articulos_um_deposito.list;
            this.filteredArticulos = this.form_carga_articulos.get('articulo').valueChanges.pipe(
                startWith(''),
                map((value) => this._filterArticulos(value)),
            );
            resolve(true);
        });

        //Esta funcion la realizamos para listar los articulos sin que se repitan.
        articulos_um_deposito.list.forEach(item => {
            const existeArticulo = this.articulos_list.find(articulo => articulo.idarticulo === item.idarticulo);
            if (!existeArticulo) {
                let articulo = { idarticulo: item.idarticulo, codigo_interno: item.codigo_interno, descripcion: item.descripcion };
                this.articulos_list.push(articulo);
            };
        });
    }

    // ################# Metodo de filtrado para inputs autocomplete #################

    /**
     * Funcion generica para encontrar un item en un arreglo, parametrizable.
     * @param value Valor ingresado por buscador
     * @param array Arreglo donde se compara la busqueda
     * @param param Campo por el que se desea buscar coincidencias
     * @returns Resultado de busqueda
     */
    private _filterTextGenerico(value: string, array: any[], param: string) {
        const filterValue = value.toString().toLowerCase();
        return array.filter(res => {
            const result = res[`${param}`].toString().toLowerCase().includes(filterValue);
            return result;
        });
    }

    private _filterUsuarios(usuario: IUsuario): IUsuario[] {
        const filterValue = usuario.toString().toLowerCase();
        return this.usuarios.filter(usuario => (usuario.nombre.toLowerCase().includes(filterValue)) && usuario.codigo !== this.user.id);
    }

    /**
    * Esta funcion es para filtrar las ventas segn el input que se haya ingresado en el buscador de ventas.
    * @param value 
    * @returns 
    */
    private _filterVentas(value: string): any[] {
        const filterValue = value.toString().toLowerCase();
        return (this.ventas || []).filter(venta => {
            const cliente = venta.cliente.toString().toLowerCase().includes(filterValue);
            const fecha = venta.fecha.toString().toLowerCase().includes(filterValue);
            return cliente || fecha;
        });
    }

    /**
    * Esta funcion es para filtrar los artculos segn el input que se haya ingresado en el buscador de articulos.
    * @param value 
    * @returns 
    */
    private _filterArticulos(value: string): IArticulo[] {
        const filterValue = value.toString().toLowerCase();
        return this.articulos_list.filter(articulo => {
            const codigo_interno = articulo.codigo_interno.toString().toLowerCase().includes(filterValue);
            const descripcion = articulo.descripcion.toString().toLowerCase().includes(filterValue);
            return codigo_interno || descripcion;
        });
    }

    // ################# Metodos para obtener textos de input #################

    /**
     * Esta funcion obtiene el texto del input que lo utilice en [displayWith]="getTextGenerico"
     * @param option Type: Unidad Funcional
     * @param option Type: Complejo
     * @param option Type: Usuario
     * @param option Type: Venta
     * @returns 
     */
    getTextGenerico(option) {
        if (option.codigo) {
            return option.apellido ? `${option.nombre} ${option.apellido}` : `${option.nombre}`;
        }
        if (option.usuario) {
            return option.usuario;
        }
        if (option.idunidadfuncional) {
            return option.descripcion_complejo_uf;
        }
        if (option.idcomplejo) {
            return option.descripcion_complejo;
        }
        if (option.idventagenerica) {
            return `N ${option.numero_venta} | ${option.cliente} ~ ${option.fecha}`;
        } else if (option.idventagenerica === null) {
            return `Sin Asignacin`;
        }
        return option;
    }

    getTextDeposito(option) {
        if (option.descripcion) {
            return option.descripcion;
        }
        if (option.deposito) {
            return option.deposito;
        }
        return option;
    }

    /**
    * Obtenemos la descripcion del articulo seleccionado.
    * @param articulo 
    * @returns 
    */
    getTextArticulo(articulo: IArticulo): string {
        return articulo?.idarticulo ? `${articulo.codigo_interno} - ${articulo.descripcion}` : '';
    }

    // ################# Metodos extra #################

    /**
    * Funcion para contar las palabras en las observaciones
    */
    getPalabrasLength(): number {
        const observaciones = this.form.get('observaciones').value;
        return observaciones ? observaciones.length : 0;
    }

    /**
    * Funcion para verificar si se ha alcanzado el lmite de caracteres en las observaciones
    */
    isPalabraMaxLengthReached(): boolean {
        return this.getPalabrasLength() <= this.maxPalabras;
    }


    /**
     * Este metodo sirve para agregar o quitar las validaciones de requeridos a los campos del formulario de carga de articulos.
     */
    updateFormValidators() {
        if (this.mov_art_list.length === 0) {
            // Si la grilla est vaca, hacer que los campos sean requeridos
            this.form_carga_articulos.get('articulo').setValidators(Validators.required);
            this.form_carga_articulos.get('cantidad').setValidators([Validators.required, Validators.min(1)]);
            this.form_carga_articulos.get('unidadmedida').setValidators(Validators.required);
        } else {
            // Si hay elementos en la grilla, quitar la validacin de requerido
            this.form_carga_articulos.get('articulo').clearValidators();
            this.form_carga_articulos.get('cantidad').setValidators([Validators.min(1)]);
            this.form_carga_articulos.get('unidadmedida').clearValidators();
        }

        // Asegurarse de que Angular reevale las validaciones
        this.form_carga_articulos.get('articulo').updateValueAndValidity();
        this.form_carga_articulos.get('cantidad').updateValueAndValidity();
        this.form_carga_articulos.get('unidadmedida').updateValueAndValidity();
    }

    /**
    * Filtro para buscar en la grilla de articulos
    * @param filterValue 
    */
    applyFilter(filterValue: string): void {
        this.dataSource.filter = filterValue.trim().toLowerCase();
    }
}