import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { IUsuario } from '@core/user/user.types';
import { fuseAnimations } from '@fuse/animations';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { IDeposito } from 'app/modules/stock/depositos/models/deposito';
import { IEntrega } from '../../models/entrega';
import { DatePipe } from '@angular/common';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { MY_DATE_FORMATS } from '@shared/constants/date-format';
import { EntregasService } from '../../service/entregas.service';
import { debounceTime, firstValueFrom, map, Observable, startWith, Subject, takeUntil } from 'rxjs';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { IUnidadFuncional } from 'app/modules/inmuebles/unidades-funcionales/models/unidad-funcional';
import { IComplejo } from 'app/modules/inmuebles/complejos/models/complejos';

@Component({
  selector: 'app-filter-menu-entregas',
  templateUrl: './filter-menu.component.html',
  styleUrls: ['./filter-menu.component.scss'],
  providers: [
    { provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS },
  ],
  animations: fuseAnimations
})
export class FilterMenuComponent implements OnInit {
  public formFilters: FormGroup;
  public proveedorespp: any[];
  filterDeposito: IDeposito[] = [];
  filteredVentas: Observable<any>;
  filteredUnidadesFuncionales: Observable<IUnidadFuncional[]>;
  filteredComplejos: Observable<IComplejo[]>;
  unidades_funcionales: IUnidadFuncional[] = [];
  complejos: IComplejo[] = [];
  ventas: any[] = [];
  usuarios: IUsuario[] = [];
  filteredUsuarioResponsable: Observable<IUsuario[]>;
  filteredUsuarioEncargado: Observable<IUsuario[]>;
  filterFechas: any = [];
  movimientos_internos_list: IEntrega[] = []; //Se crea por s hace falta crear una copia de los datos y manipularlos de una forma ms personalizada
  color_primario: string;
  color_secundario: string;
  default_color: string = 'bg-primary';
  private _unsubscribeAll: Subject<any> = new Subject<any>();
  asignacion_entrega: number;

  constructor(
    @Inject('MENU_DATA') public data: any,
    private _menuService: JedsMenuService,
    private datePipe: DatePipe,
    private _entregasService: EntregasService,
    private _empresaService: EmpresaService
  ) { }

  ngOnInit(): void {
    this.formFilters = new FormGroup({
      filtroFechaInicio: new FormControl(('')),
      filtroFechaFin: new FormControl(('')),
      usuario_responsable: new FormControl(('')),
      usuario_encargado: new FormControl(('')),
      deposito: new FormControl((-1))
    });
    // Subscribe to empresa data
    this._empresaService.empresa$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe(empresa => {
        this.color_primario = empresa.color_primario;
        this.color_secundario = empresa.color_secundario;
        this.asignacion_entrega = empresa.asignacion_entrega;
      });

    // Load empresa data
    this._empresaService.getEmpresa();
    this.getDepositos();
    this.getUsuarios();
    this.getAsignacionEntrega();
    this.formFilters.patchValue(this.data.filter);
  }

  getAsignacionEntrega() {
    if (this.asignacion_entrega === 0) {
      this.formFilters.addControl('orden_servicio', new FormControl(('')));
      this.getVentas();
    } else if (this.asignacion_entrega === 1) {
      this.formFilters.addControl('unidad_funcional', new FormControl(('')));
      this.formFilters.addControl('complejo', new FormControl(('')));
      this.getUnidadesFuncionales();
      this.getComplejos();
    }
  }

  resetFilters() {
    if (this.asignacion_entrega === 0) {
      this.formFilters.reset({
        filtroFechaInicio: '',
        filtroFechaFin: '',
        usuario_responsable: '',
        usuario_encargado: '',
        deposito: -1,
        orden_servicio: '',
      });
    } else if (this.asignacion_entrega === 1) {
      this.formFilters.reset({
        filtroFechaInicio: '',
        filtroFechaFin: '',
        usuario_responsable: '',
        usuario_encargado: '',
        deposito: -1,
        unidad_funcional: '',
        complejo: '',
      });
    }

  }

  onCloseMenu(data: any = null) {
    if (data) {
      data.filtroFechaInicio = this.datePipe.transform(this.formFilters.get('filtroFechaInicio').value, 'yyyy-MM-dd');
      data.filtroFechaFin = this.datePipe.transform(this.formFilters.get('filtroFechaFin').value, 'yyyy-MM-dd');
    }
    this._menuService.close(data);
  }

  /**
  * Funcion para limpiar los filtros de rango de fechas
  */
  limpiarFechas() {
    this.formFilters.get('filtroFechaInicio').setValue('');
    this.formFilters.get('filtroFechaFin').setValue('');
  }

  /**
   * Esta funcion es para listas los usuarios en el filtro de grilla.
   */
  async getUsuarios() {
    const Usuarios = await firstValueFrom(this._entregasService.getUsuarios());
    this.usuarios = Usuarios.list;
    this.filteredUsuarioResponsable = this.formFilters.controls['usuario_responsable'].valueChanges.pipe(
      debounceTime(400),
      startWith(''),
      map(value => this._filterUsuaurio(value))
    )
    this.filteredUsuarioEncargado = this.formFilters.controls['usuario_encargado'].valueChanges.pipe(
      debounceTime(400),
      startWith(''),
      map(value => this._filterUsuaurio(value))
    )
  }

  /**
    * Esta funcion es para filtrar los proveedores segn el input que se haya ingresado en el input de proveedor.
    * @param value 
    * @returns 
    */
  private _filterUsuaurio(value: string) {
    const filterValue = value.toLowerCase();
    return this.usuarios.filter(usuario =>
      usuario.nombre.toLowerCase().includes(filterValue)
    );
  }

  /**
   * Esta funcion es para listas los depositos en el filtro de grilla.
   */
  async getDepositos() {
    const stk_list = await firstValueFrom(this._entregasService.getDepositos(true));
    this.filterDeposito = stk_list.list;
  }

  /**
  * Funcion que lista las ventas.
  */
  async getVentas(): Promise<any> {
    const ventas = await firstValueFrom(this._entregasService.getVentas());
    this.ventas = ventas.list;
    this.filteredVentas = this.formFilters.get('orden_servicio').valueChanges.pipe(
      startWith(''),
      map((value) => this._filterVentas(value)),
    );
  }

  /**
  * Esta funcion es para filtrar las ventas segn el input que se haya ingresado en el buscador de ventas.
  * @param value 
  * @returns 
  */
  private _filterVentas(value: any): any[] {
    const filterValue = value?.idventagenerica ? value.idventagenerica.toString().toLowerCase() : value.toString().toLowerCase(); // PErdon por no ser tan prolijo, es una chanchada esto, no tengo tiempo ;(
    return this.ventas.filter(venta => {
      const cliente = venta.cliente.toString().toLowerCase().includes(filterValue);
      const fecha = venta.fecha.toString().toLowerCase().includes(filterValue);
      return cliente || fecha;
    });
  }

  /**
  * Obtenemos el cliente y fecha de dicha venta seleccionada.
  * @param venta 
  * @returns 
  */
  getTextVenta(venta: any): string {
    return venta ? `${venta.cliente} ~ ${venta.fecha}` : '';
  }

  /**
* Funcion que lista los complejos.
*/
  async getComplejos(): Promise<any> {
    const complejos = await firstValueFrom(this._entregasService.getComplejos());
    this.complejos = complejos.list;
    this.filteredComplejos = this.formFilters.get('complejo').valueChanges.pipe(
      startWith(''),
      map((value) => this._filterTextGenerico(value, this.complejos, 'descripcion_complejo')),
    );
  }

  /**
  * Funcion que lista las unidades funcionales.
  */
  async getUnidadesFuncionales(): Promise<any> {
    const unidades_funcionales = await firstValueFrom(this._entregasService.getUnidadesFuncionales());
    this.unidades_funcionales = unidades_funcionales.list;
    this.filteredUnidadesFuncionales = this.formFilters.get('unidad_funcional').valueChanges.pipe(
      startWith(''),
      map((value) => this._filterTextGenerico(value, this.unidades_funcionales, 'descripcion_complejo_uf')),
    );
  }

  /**
  * Obtenemos el cliente y fecha de dicha venta seleccionada.
  * @param uf 
  * @returns 
  */
  getTextUnidadFuncional(uf: any): string {
    return uf ? uf.descripcion_complejo_uf : '';
  }

  /**
  * Obtenemos el cliente y fecha de dicha venta seleccionada.
  * @param complejo 
  * @returns 
  */
  getTextComplejo(complejo: any): string {
    return complejo ? complejo.descripcion_complejo : '';
  }

  /**
   * Funcion generica para encontrar un item en un arreglo, parametrizable.
   * @param value Valor ingresado por buscador
   * @param array Arreglo donde se compara la busqueda
   * @param param Campo por el que se desea buscar coincidencias
   * @returns Resultado de busqueda
   */
  private _filterTextGenerico(value: string, array: any[], param: string) {
    const filterValue = value.toString().toLowerCase();
    return array.filter(res => {
      const result = res[`${param}`].toString().toLowerCase().includes(filterValue);
      return result;
    });
  }

  /**
  * Esta funcion obtiene el texto del input que lo utilice en [displayWith]="getTextGenerico"
  * @param option Type: Unidad Funcional
  * @param option Type: Complejo
  * @param option Type: Usuario
  * @param option Type: Venta
  * @returns 
 */
  getTextGenerico(option) {
    if (option.codigo) {
      return option.apellido ? `${option.nombre} ${option.apellido}` : `${option.nombre}`;
    }
    if (option.usuario) {
      return option.usuario;
    }
    if (option.idunidadfuncional) {
      return option.descripcion_complejo_uf;
    }
    if (option.idcomplejo) {
      return option.descripcion_complejo;
    }
    if (option.idventagenerica) {
      return `${option.cliente} ~ ${option.fecha}`;
    } else if (option.idventagenerica === null) {
      return `Sin Asignacin`;
    }
    return option;
  }
}